.App {
  text-align: center;
  color: white;
}

.App-logo {
  margin-top: 64px;
  animation: App-logo-spin infinite 40s linear;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 40s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: #444b5a;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  box-shadow: 0px 10px 10px rgba(255, 255, 255, 0.39);
}

.App-link {
  color: #61dafb;
}
.Title{
  color: #61dafb;
  font-size: 45px;
  text-shadow:
  -4px 4px 0 #000000,
  -3px 3px 0 #000000c2,
  -2px 2px 0 #0000007e;
}
p{
  font-weight: bold;
}
img{
  image-rendering: pixelated;
  width: 128px;
}
.shadowed{
  color: #282c34;
  text-shadow:
  -1px 1px 0 #37798b,
  -3px 3px 0 #37798b,
  -2px 2px 0 #37798b;
}
.smallfont{
  font-size: small;
}

nav{
  /* height: 60px; */
  background-color: #62d9fb;
  font-size: 32px;
  position: sticky;
}
.container{
  /* background-color: #3d465c; */
  margin-left: 150px;
  margin-right: 150px;
  box-shadow: 3px -3px 10px rgb(82, 92, 97) inset, -3px 3px 10px rgb(18, 18, 19) inset;
  border-radius: 8px;
}
code{
  margin-bottom: 64px;
  text-shadow:
  -4px 4px 0 #000000,
  -3px 3px 0 #000000c2,
  -2px 2px 0 #0000007e;
}

.linkImg{
  image-rendering: auto;
  width: 48px;
  margin-top: 16px;
  margin-bottom: 16px;
  margin-right: 8px;
  margin-left: 8px;
}